.nav {
  display: flex;
  width: 68.5%;
  justify-content: space-between;
  align-items: center;
}

.nav_list {
  display: flex;
  margin: 0;
  padding: 0;
}

.nav__item {
  font-size: 14px;
  padding-left: 14px;
  padding-right: 28px;

  &:first-child {
    padding-left: 0;
  }

  &:last-child {
    padding-right: 0;
  }
}

.nav__link {
  color: white;
  font-family: "Roboto Condensed", sans-serif;

  &:hover {
    opacity: 0.7;
  }

  &:active,
  &:focus {
    opacity: 0.3;
  }
}

@media only screen and (max-width: 1199px) {
  .nav {
    width: 79%;
  }

  .nav__item {
    padding-left: 14px;
    padding-right: 20px;

    &:first-child {
      padding-left: 25px;
    }
  }
}

@media only screen and (max-width: 767px) {
  .nav {
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
  }

  .nav_list {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }
}

