*,
*::after,
*::before {
  box-sizing: border-box;
}

  html {
    scroll-behavior: smooth;
    overflow-x: hidden;
  }

  body {
    position: relative;
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-style: normal;
    font-weight: 300;
    font-family: "Bebas Neue", "Roboto Condensed", sans-serif;
    line-height: 24px;
    background: white;
    color: rgba(16, 38, 84, 1);
  }

  li {
    list-style: none;
  }

  a {
    text-decoration: none;
  }

  img {
    height: auto;
    max-width: 100%;
    border-style: none;
  }

  h3 {
    margin: 0;
    font-family: "Roboto Condensed", sans-serif;
  }

  p {
    margin: 0;
    font-family: "Roboto Condensed", sans-serif;
  }

  span {
    font-family: "Roboto Condensed", sans-serif;
  }

