.timetable {
  padding: 130px 0;
}

.timetable__wrap {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  max-width: 1366px;
}

.timetable__workout-wrap--second {
  display: none;
}

.timetable__heading {
  margin: 0 0 65px;
  font-size: 52px;
  text-align: center;
}

.timetable__workout-wrap {
  width: 96%;
  padding: 0 65px;
  margin: 0 auto;
}

.timetable__days-list {
  display: flex;
  justify-content: space-between;
  width: 98%;
  margin: 0 auto 8px;
  padding: 0 0 0 75px;
}

.timetable__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 66px;
  margin-right: 10px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 18px;
  font-weight: 400;
  border: 1px solid rgba(28, 51, 116, 1);

  &:last-child {
    margin-right: 0;
  }
}

.timetable__workout-list {
  display: flex;
  justify-content: space-between;
  width: 98%;
  margin: 0 auto;
  padding: 0;
}

.timetable__workout-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 110px;
  margin-right: 9px;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 18px;
  font-weight: 400;
  background-color: rgba(248, 249, 252, 1);

  &:last-child {
    margin-right: 0;
  }
}

.timetable__workout-item--time {
  width: 65px;
  margin-bottom: 7px;
  border: 1px solid rgba(28, 51, 116, 1);
  background: none;
}

@media only screen and (max-width: 1199px) {
  .timetable {
    padding: 88px 0 0
  }

  .timetable__wrap {
    overflow: auto;
    width: 93%;
    padding: 0;
    display: flex;
    flex-direction: row;

    &::-webkit-scrollbar-track {
      background: rgba(254, 235, 239, 1);
    }

    &::-webkit-scrollbar {
      width: 205px;
      height: 5px;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(237, 2, 51, 1);
      border-radius: 8px;
    }
  }

  .timetable__workout-wrap--second {
    display: block;
  }

  .timetable__heading {
    margin: 0 0 47px;
    font-size: 45px;
  }

  .timetable__workout-wrap {
    padding: 0;
  }

  .timetable__item--5,
  .timetable__item--6,
  .timetable__item--7 {
    display: none;
  }

  .timetable__workout-item--5,
  .timetable__workout-item--6,
  .timetable__workout-item--7 {
    display: none;
  }
}
